export default {
	data() {
		return {
			code: "xxx",
			date: '2018-12-25',
			textareaAValue:'',
		};
	},
	mounted() {
		this.code = this.$store.state.gsQRcode.split('?')[1] ||  '测试'
	},
	methods: {
		DateChange(e) {
			this.date = e.detail.value
		},
		textareaBInput(e) {
			this.textareaAValue = e.detail.value
		},
		lu(){
			uni.request({
			        url: this.$store.state.url + 'System/SupplementCusEqu',
			        method: 'POST',
			        data: {
			            InchwellCode :this.code.split('=')[1],
						BeginRunDate :this.date
			        },
			        header: {
			            'Token': this.$store.state.token
			        },
			        success: (res) => {
							if(JSON.parse(res.data).Code == 0){
								 uni.showToast({
						        title: "录入成功",
						        icon: "none"
						    })
							}
			        }
			    });
		},
		sao(){
			uni.scanCode({
			    success: function (res) {
					this.code = res.result.split('=')[0]
			    }
			});
		}
	}
}
